import React, { createContext, useReducer } from "react";
import AppReducer from "./AppReducer";
import axios from "axios";

// Initial state
const initialState = {
  transactions: [],
  error: null,
  loading: true
};

// Create context
export const GlobalContext = createContext(initialState);

// Provider component
export const GlobalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(AppReducer, initialState);

  let backendDomainName;
  if (process.env.NODE_ENV === "production") {
    backendDomainName = "https://brad-expense-tracker.herokuapp.com";
  } else {
    backendDomainName = "http://localhost:5000";
  }

  // If connection to server is refused
  let noServerResponseError = {
    data: {
      error: {
        success: false,
        error: "Couldn't connect to server"
      }
    }
  };

  // Actions
  async function getTransactions() {
    try {
      const res = await axios.get(`${backendDomainName}/api/v1/transactions`);

      dispatch({
        type: "GET_TRANSACTIONS",
        payload: res.data.data
      });
    } catch (err) {
      // Server connection refused or not
      if (!err.response) {
        err.response = noServerResponseError;
      }

      dispatch({
        type: "TRANSACTION_ERROR",
        payload: err.response.data.error
      });
    }
  }

  async function deleteTransaction(id) {
    try {
      await axios.delete(`${backendDomainName}/api/v1/transactions/${id}`);

      dispatch({
        type: "DELETE_TRANSACTION",
        payload: id
      });
    } catch (err) {
      // Server connection refused or not
      if (!err.response) {
        err.response = noServerResponseError;
      }

      dispatch({
        type: "TRANSACTION_ERROR",
        payload: err.response.data.error
      });
    }
  }

  async function addTransaction(transaction) {
    const config = {
      headers: {
        "Content-Type": "application/json"
      }
    };

    try {
      const res = await axios.post(
        `${backendDomainName}/api/v1/transactions`,
        transaction,
        config
      );

      dispatch({
        type: "ADD_TRANSACTION",
        payload: res.data.data
      });
    } catch (err) {
      // Server connection refused or not
      if (!err.response) {
        err.response = noServerResponseError;
      }

      dispatch({
        type: "TRANSACTION_ERROR",
        payload: err.response.data.error
      });
    }
  }

  return (
    <GlobalContext.Provider
      value={{
        transactions: state.transactions,
        getTransactions,
        error: state.error,
        loading: state.loading,
        deleteTransaction,
        addTransaction
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};
